import * as Flags from '../flags';
import { installHandler } from '../effect';
export function clear(config) {
    let layers = 0;
    if (!config) {
        layers = Flags.Depth.ALL_LAYERS;
    }
    else if (typeof config === 'number') {
        layers = config;
    }
    else if (typeof config === 'string') {
        const parts = config.split(/[,|]/g);
        layers = parts.reduce((out, v) => {
            if (typeof v === 'number')
                return out | v;
            const depth = Flags.Depth[v] || 0;
            return out | depth;
        }, 0);
    }
    else {
        throw new Error('Invalid config for clear effect: ' + JSON.stringify(config));
    }
    return clearAction.bind(undefined, layers);
}
export function clearAction(layers, action) {
    if (!layers)
        return action.didNothing();
    const cell = action.map.cell(action.x, action.y);
    if (cell.clearDepth(layers)) {
        action.didSomething();
    }
    else {
        action.didNothing();
    }
}
installHandler('clear', clear);
